iones desarrollarán una que, de nuevo, puede combinar varios criterios y medir al proveedor en diferentes dimensiones. Sin embargo, una encuesta no es la única forma de evaluar a los proveedores y, en muchos casos, ni siquiera es la óptima. La información obtenida en la revaluación podría muy bien ayudarle con la necesidad de analizar los datos del proveedor.")]),_c('div',{staticClass:"cajon color-acento-contenido p-4 mb-4",attrs:{"data-aos":"flip-down"}},[_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-down"}},[_vm._v("Así que, tanto si separa al proveedor como si no, se deben decidir los criterios de evaluación. Si una empresa no tiene muchos proveedores, quizá baste con la misma evaluación para todos ellos. La idea es que esta se beneficie de la revaluación de proveedores, de modo que pueda controlar su rendimiento y decidir cuándo elogiarlos y cuándo emitir una bandera roja. Al final, un mal proveedor proporcionará productos y servicios mediocres y, en última instancia, causará problemas con los clientes.")])])])]),_c('div',{staticClass:"row justify-content-center align-items-center my-5"},[_c('div',{staticClass:"col-lg-12"},[_c('div',{staticClass:"tarjeta position-relative rounded-0 bg7_1",attrs:{"data-aos":"flip-down"}},[_c('div',{staticClass:"row justify-content-start align-items-center"},[_c('div',{staticClass:"col-11"},[_c('div',{staticClass:"p-4 pe-5"},[_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-down"}},[_vm._v("El requisito establecido en la cláusula 8.4 de la norma ISO 9001 exige analizar los datos de los proveedores. La intención de la norma es mostrar que la empresa conoce el rendimiento de los proveedores y que, si no están funcionando bien, tiene la oportunidad de hacer algo al respecto. Si no se analizan los datos, no se tiene visibilidad y, por tanto, de qué sirve rellenar un montón de formularios en papel para los proveedores, apilarlos o archivarlos y no mirarlos nunca. Sí, es posible que la empresa haga las evaluaciones según lo previsto, pero si no mira los resultados, ese ejercicio no tiene ningún valor. Por eso existe este requisito, para asegurarse de que se analizan los datos de los proveedores con el fin de saber lo que ocurre con ellos. ")])])]),_c('div',{staticClass:"col-auto"},[_c('div',{staticClass:"imagen-absolute2"},[_c('img',{staticClass:"img-t",attrs:{"data-aos":"zoom-in","src":require("@/assets/curso/temas/71.svg"),"alt":""}})])])])])])]),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down"}},[_vm._v("A continuación, se muestran algunos buenos indicadores para analizar:")]),_c('div',{staticClass:"titulo-sexto color-primario mb-3",attrs:{"data-aos":"fade-right"}},[_c('h5',[_vm._v("Figura 2")]),_c('p',{staticClass:"mb-0"},[_c('i',[_vm._v("Indicadores para analizar")])])]),_c('img',{staticClass:"img-a img-t mb-5",attrs:{"data-aos":"zoom-in","src":require("@/assets/curso/temas/72.svg"),"alt":""}}),_c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-lg-5 my-3"},[_c('img',{staticClass:"img-a img-t",attrs:{"data-aos":"zoom-in","src":require("@/assets/curso/temas/73.png"),"alt":""}})]),_c('div',{staticClass:"col-lg-7 my-3"},[_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("Como recomendación, es importante pensar en el análisis de datos al decidir los métodos de revaluación de proveedores. Si la revaluación es capaz de producir algún tipo de datos que puedan ser analizados, entonces cumplirá estos requisitos simultáneamente; de lo contrario, si se decide revaluar a los proveedores mediante una encuesta, hay que idear una forma diferente de analizar los datos de los proveedores, a menos que se puedan sopesar las respuestas de la encuesta y, de alguna manera, ser capaz de saber cómo se desempeñan los proveedores con respecto a un objetivo definido. Si se puede decir que el proveedor A rinde un 90 %, el proveedor B rinde un 80 % y el objetivo para todos los proveedores es el 95 % —en la dimensión que decida—, entonces son datos que se pueden comparar fácilmente y actuar en consecuencia.")]),_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("Con una información clara, la toma de decisiones sobre los proveedores será una ventaja para la empresa. Se podrá, entonces, dejar de lado a un proveedor por su bajo rendimiento, ponerlo a prueba o darle más trabajo por su excelente rendimiento. ")])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-10 my-3"},[_c('div',{staticClass:"cajon color-acento-contenido p-4",attrs:{"data-aos":"flip-down"}},[_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-down"}},[_vm._v("Para un estudio completo del tema, recuerda explorar todos los recursos disponibles en este componente formativo que encuentras en el menú principal, tales como material complementario, actividad didáctica y glosario, entre otros.")])])])])])}]

